#myHeader {
    margin-top:5px;
}

#myHeader .brand {
    display:inline-block;
    position: relative;
    top: -10px;
    
}

@media (max-width:820px){
    .pullup-animation {
        opacity: 0;
        -webkit-transition: all 300ms ease-in;
        -o-transition: all 300ms ease-in;
        transition: all 300ms ease-in;
        -webkit-transform: translate3d(0px, -50px, 0px);
        -ms-transform: translate(0px, -50px);
        transform: translate3d(0px, -50px, 0px);
    }
}

#myHeader a.navbar-item{
    font-weight:500;
    color: #0b0f1f;
}

#myHeader {
    position: relative;
  }

  
#myHeader:before, #myHeader:after {
content: "";
position: absolute;
bottom: -3px;
left: -3px;
}

#myHeader:after {
right: -3px;
height: 3px;
background-image: -webkit-gradient(linear, 0 0, 100% 0, from(#0048BA), to(transparent));
background-image: -webkit-linear-gradient(left, #0048BA, transparent);
background-image: -o-linear-gradient(left, #0048BA, transparent);
}

@media (max-width:500px){
    #myHeader .brand {
        top: -15px;        
    }

    #myHeader .brand p {
        font-size:1rem;
    }

    #myHeader div.navbar-burger.burger {
        display: inline-block;
    }

    #myHeader a.navbar-item {
        text-align: center;
        display:block;
        padding:0;
    }

    #myHeader #navMenuColorwhite-example div div {
        margin-top: -10px;
    }

    #myHeader div.navbar-end {
        text-align: center;
        display:block;
    }
}


#myHeader .brand .title {
    font-family: 'Karla', sans-serif;
}

#myHeader .logoGlobe {
    margin-left:15px;
    height: 70px;
}

#myHeader .navbar-item {
    margin-top:15px;
    display:inline-block;
    z-index:99;  
}

#myHeader .navbar-item:hover {
    margin-top:15px;
    display:inline-block;
    z-index:99;  
}




.navbar-menu .is-active {
    z-index:999;
}

nav.breadcrumb.has-succeeds-separator {
    padding-left:5%;
    position:relative;
    top:3px;
  }


